import './Slideshow.css';
import React, {useState, useEffect} from 'react';

function Slideshow({children}) {

    const [slideshowIndex, setSlideshowIndex] = useState(0);

    useEffect(() => {
        const interval = setInterval(() => {
        setSlideshowIndex(function (_slideshowIndex) {

            return (_slideshowIndex + 1) % children.length;

        });
        }, 2500);
        return () => clearInterval(interval);
    }, []);

    const finalFields = React.Children.map(children, (child, index) => {
        return React.cloneElement(child, {
            style: (slideshowIndex === index) ? ({opacity: 1}) : ({}) 
        });
      });

  return (
    <div className="Slideshow">
        {
            finalFields
        }
    </div>
  );
}

export default Slideshow;